.coach-swiper__content {
  position: relative;
  width: 100%;
  margin: -70px 0;

  @include vp-1199 {
    width: 83.48%;
    margin: -70px auto;
  }

  @include vp-767 {
    width: calc(226 / 290 * 100%);
  }
}

.coach-swiper__wrapper {
  height: 100%;
  margin: 0;
  padding: 70px 0;
}

.coach-swiper__slide {
  width: calc((100% - 120px) / 4);

  @include vp-1199 {
    width: calc((100% - 30px) / 2);
  }

  @include vp-1199 {
    width: 100%;
  }
}

.coach-swiper {
  position: relative;
  width: 100%;

  @include vp-767 {
    max-width: 500px;
    margin: 0 auto;
  }
}

.coach-swiper__button {
  position: absolute;
  z-index: 100;
  top: 46.3%;
  display: block;
  width: 27px;
  height: 33px;
  margin: 0;
  padding: 0 5px;
  background-color: transparent;
  background-image: url("data:image/svg+xml,%3Csvg width='18' height='34' viewBox='0 0 18 34' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M17 33L0.999999 17L17 0.999998' stroke='%23AFAFAF'/%3E%3C/svg%3E%0A");
  content: "";
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  border: none;
  cursor: pointer;
  outline: 2px solid rgba($color-dark-gray, 0);
  transition: outline $trans-default;
  box-sizing: content-box;

  @include vp-767 {
    top: 46.2%;
    width: 32px;
    height: 32px;
    padding: 0;
    background-size: 11px 20px;
  }

  &:disabled {
    opacity: 0.5;
    pointer-events: none;
  }

  @include focus {
    outline: 2px solid $color-default-black;
  }

  @include hover {
    outline: 2px solid rgba($color-dark-gray, 0.8);
  }

  &:not(.crutch):active {
    opacity: 0.2;
  }

  &--prev {
    left: -67px;

    @include vp-1199 {
      left: -11px;
    }
  }

  &--next {
    right: -67px;
    transform: rotate(180deg);

    @include vp-1199 {
      right: -11px;
    }
  }
}
