}
@media (max-width: 767px) {
  .footer__list {
    display: block;
    margin-top: -28px;
    margin-left: -11px;
    column-count: auto;
    column-width: 125px;
    column-gap: 31px;
  }
  .footer__list::before {
    display: list-item;
    width: 100%;
    height: 39px;
    content: "";
  }
}

.footer__link {
  position: relative;
  display: block;
  margin: 4px 10px;
  padding: 0 10px;
  font-size: 14px;
  line-height: 16px;
  color: inherit;
}
