    width: fit-content;
    margin: 2px 0;
    padding: 10px;
    font-size: 16px;
    line-height: 19px;
  }
}
.footer__link::before {
  position: absolute;
  top: -5px;
  bottom: -5px;
  left: -5px;
  right: -5px;
  display: block;
  border: 2px solid #ffffff;
  content: "";
  opacity: 0;
  transition: opacity 0.2s ease-out;
}
@media (hover: hover) {
  .footer__link:hover:not(.focus-visible)::before {
